<template>
    <div class="header-main">
        <div class="user-main">
            <img v-if="user_data.id" @click="handleCommand('Home')" :src="user_data.image" alt="User Image">
            <span v-else>
                <img :src="user_data.image" alt="User Image">
            </span>
            <span class="name">{{ user_data.name }}</span>
        </div>
        <div class="horizontal-links">
            <router-link to="/" custom v-slot="{ navigate, isActive }">
                <button @click="navigate" :class="{ active: isActive }">首页</button>
            </router-link>
            <router-link to="/Login" custom v-slot="{ navigate, isActive }" v-if="!isLogin">
                <button @click="navigate" :class="{ active: isActive }">登录</button>
            </router-link>
            <router-link to="/Register" custom v-slot="{ navigate, isActive }" v-if="!isLogin">
                <button @click="navigate" :class="{ active: isActive }">注册</button>
            </router-link>
            <router-link to="/ChatAI" custom v-slot="{ navigate, isActive }">
                <button @click="navigate" :class="{ active: isActive }">AI助手</button>
            </router-link>
            <router-link to="/Home" custom v-slot="{ navigate, isActive }">
                <button @click="navigate" :class="{ active: isActive }">我的</button>
            </router-link>
            <button @click="handleCommand('logout')" v-if="isLogin">注销</button>
        </div>
    </div>
</template>

<script>
"use strict";
import { useUser } from '@/store/useUser';
import { ref, onMounted, computed } from 'vue';
import { useRouter,useRoute } from 'vue-router';
import { notice } from './notice';
export default {
    setup() {
    const user = useUser();
    const isLogin = computed(function(){
        return user.token;
    });
    const user_data = ref({});
    const router=useRouter();

    const initPage = async () => {
        try {
        const loading = user.initData();
        if (loading)
            user_data.value = user.user_data;
        else {
            user_data.value.name = '游客';
            user_data.value.image = 'https://tse3-mm.cn.bing.net/th/id/OIP-C.HXHAQvjrX1gCRlcANzmqPgAAAA?rs=1&pid=ImgDetMain'; 
            user_data.value.id = null;
        }
        } catch (error) {
        console.log("error", error);
        }
    };

    const handleCommand = (command) => {
        switch (command) {
        case 'index':
            router.push({path:"/"});
            break;
        case 'login':
            router.push({path:"/Login"});
            break;
        case 'register':
            router.push({path:"/Register"});
            break;
        case 'aiAssistant':
            router.push({path:"/ChatAI"});
            break;
        case 'logout':
            out();
            break;
        case "Home":
            if(!isLogin.value){
                router.push({path:"/Login"});
                notice("请先登陆","",2000);
            }else{
                router.push({path:"/Home"});
            }
            break;
        default:
            break;
        }
    };

    const out=()=>{
        localStorage.clear();
        user.user_data=null;
        user.token=null;
        clearCookies();
        location.reload();
    }
    const clearCookies=()=>{
        document.cookie='token=;user_date=;expires=Thu, 01 Jan 1970 00:00:00 GMT;path=/'
    }

    onMounted(() => {
        initPage();
    });

    return {
        user_data,
        isLogin,
        handleCommand,
    };
    }
};
</script>
