"use strict";

import { ElNotification } from "element-plus";
function notice(title,message,time=2000){
    ElNotification({
        title:title,
        message:message,
        type:'error',
        duration:time
    });
}
import { createRouter,createWebHistory } from "vue-router";

const routes=[
    {
        path:"/",
        component:()=>import('@/page/IndexPagr.vue')
    },
    {
        path:"/blank",
        component:()=>import('@/components/BlankPage.vue')
    },
    {
        path:"/404",
        component:()=>import('@/components/NotFound.vue')
    },
    {
        path:"/Login",
        component:()=>import('@/page/LoginPage.vue')
    },
    {
        path:"/Register",
        component:()=>import('@/page/RegisterPage.vue')
    },
    {
        path:"/ChatAI",
        component:()=>import("@/page/ChatGpt.vue"),
        meta:{needAuth:true},
    },
    {
        path:"/Post/:postId",
        component:()=>import('@/page/PostIndex.vue'),
        meta:{needAuth:true}
    },
    {
        path:"/Athor/:userId",
        component:()=>import("@/page/AthorIndex.vue"),
        meta:{needAuth:true},
    },
    {
        path:"/UpdatePost/:postId",
        component:()=>import("@/components/UpdatePost.vue"),
        meta:{needAuth:true},
    },
    {
        path:"/Home",
        component:()=>import('@/page/SpacePage.vue'),
        meta:{needAuth:true},
        children:[
            {
                path:'',
                component:()=>import('@/components/MyProfile.vue'),
                meta:{needAuth:true},
            },
            {
                path:"creator",
                component:()=>import('@/components/CreateCenter.vue'),
                meta:{needAuth:true},
            },
            {
                path:"historyPost",
                component:()=>import("@/components/HistoryPage.vue"),
                meta:{needAuth:true},
            },
            {
                path:"history-comments",
                component:()=>import('@/components/comments/MyComments.vue'),
                meta:{needAuth:true},
            },
            {
                path:"dynamic",
                component:()=>import("@/components/FriendsDynamic.vue"),
                meta:{needAuth:true},
            },
            {
                path:"chat",
                component:()=>import('@/components/friends/FriendChat.vue'),
                meta:{needAuth:true},
            }
        ]
    }
]

const router=createRouter({
    history:createWebHistory(),
    routes
});

router.beforeEach((to,from,next)=>{
    if(to.meta.needAuth){
        if(!getToken()) {
            notice("你还没有登陆","请先登陆");
            return next("/Login");
        }
    }
    if(to.matched.length === 0){
        try{
            if(window.history.length&& !from.name) router.back();
            else next({path:"/"});
        }catch(e){console.error(e);next({path:"/"});}
        return ;
    }
    function getToken(){
        let token=localStorage.getItem("token");
        if(token) return true;
        else{
            let cookies=document.cookie.split(";");
            for(let cookie of cookies){
                let [key,value]=cookie;
                if(key==='token' && value)return true;
            }
            return false;
        }
    }
    next();
});

export default router;